html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  list-style-type: none;
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0px;
  padding: 0px;
}

.menu_button {
  display: none;
}

