  color: #000;
  font-family: Comfortaa;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  margin: 0px;
  padding: 0px;
}

button {
  border: 0;
  border-radius: 0;
  padding: 0;
  background-color: none;
